.section__left {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 100%;
}

.dropdown__elem__list {
  list-style: none;
  width: 90%;
}
.dropdown__elem__list .dropdown__con {
  padding: 10px;
  border-bottom: 1px solid rgb(20, 20, 20);
  margin-bottom: 10px;
}

.dropdown__title__con {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  height: fit-content;
  width: 100%;
  padding: 10px 5px;
}

.dropdown__content {
  padding: 5px;
}

*, ::before, ::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  height: 10vh;
  width: 100vw;
  background-color: rgb(49, 47, 47);
  color: rgb(241, 238, 238);
}
header .cv__title {
  transition: 0.3s ease;
}
header .cv__title:hover {
  border-bottom: 1px solid rgb(114, 109, 109);
}

@media (min-width: 1100px) {
  main {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
.hidden {
  display: none;
}